import { useAccountServiceName } from '@cetus/hooks/src/useAccountServiceName'
import useExplorer from '@cetus/hooks/src/useExplorer'
import { useAccountStore } from '@cetus/stores'
import Icon from '@cetus/ui-kit/src/components/Icon'
import { addressAbridge, cancelBubble } from '@cetus/utils'
import { Button, HStack, Popover, PopoverContent, PopoverTrigger, Text, useDisclosure, VStack } from '@chakra-ui/react'
import { useSwitchAccount } from '@mysten/dapp-kit'
import { useCallback, useRef } from 'react'
import { AddressCopyLink } from '../common'
import { AccountSwitchProps } from './type'

export default function AccountSwitch(props: AccountSwitchProps) {
  const { mutate: switchAccount } = useSwitchAccount()
  const { currentAccount, accounts, onClose: onOuterClose, isProfile } = props
  const ref = useRef()
  const { isOpen, onToggle, onClose } = useDisclosure()
  const { getServiceNames } = useAccountServiceName()
  const { accountServiceNameObj } = useAccountStore()

  const handleClick = useCallback(() => {
    if (!isOpen) {
      getServiceNames(accounts)
    }
    onToggle()
  }, [isOpen, accounts])

  const accountsObj = accounts.reduce((acc, account) => {
    acc[account.address] = account
    return acc
  }, {})
  const { getExplorerUrl } = useExplorer()
  return (
    <HStack>
      {accounts.length === 1 && (
        <VStack gap="4px" align="flex-start">
          {isProfile && accountsObj?.[currentAccount?.address]?.label && (
            <Text color="primary_gray" mb="4px">
              {accountsObj?.[currentAccount?.address]?.label}
            </Text>
          )}
          <Text fontSize="20px" color="text_caption" fontWeight={isProfile ? 500 : 400}>
            {accountServiceNameObj?.[currentAccount?.address] || addressAbridge(currentAccount?.address)}
          </Text>
        </VStack>
      )}
      {accounts.length > 1 && (
        <Popover isLazy isOpen={isOpen} onClose={onClose} placement="bottom-start" offset={isProfile ? [0, 4] : [-22, 20]}>
          <PopoverTrigger>
            <Button
              h="unset"
              variant="unstyled"
              bg="transparent"
              display="flex"
              alignItems="flex-start"
              flexDirection={isProfile ? 'column' : 'row'}
              onClick={handleClick}
            >
              {isProfile && accountsObj?.[currentAccount?.address]?.label && (
                <Text color="primary_gray" mb="8px">
                  {accountsObj?.[currentAccount?.address]?.label}
                </Text>
              )}
              <HStack gap="0">
                <Text fontSize="20px" color="text_caption" fontWeight={isProfile ? 500 : 400}>
                  {accountServiceNameObj?.[currentAccount?.address] || addressAbridge(currentAccount?.address)}
                </Text>
                {accounts.length > 1 && (
                  <Icon
                    xlinkHref="#icon-icon_descending_nor"
                    svgW="20px"
                    svgH="20px"
                    transform={isOpen ? 'rotate(-180deg)' : 'rotate(0deg)'}
                    transition="transform 0.3s"
                  />
                )}
              </HStack>
            </Button>
          </PopoverTrigger>
          {isOpen && (
            <PopoverContent
              ref={ref as any}
              w={isProfile ? '290px' : '330px'}
              bg="bg_secondary"
              borderRadius="12px"
              p="0px"
              border="1px solid"
              borderColor="border"
              position="fixed"
              zIndex="1000"
              maxH="400px"
              overflow="auto"
            >
              <VStack w="100%" p="18px" gap="20px">
                {accounts.map(account => {
                  return (
                    <HStack key={account.address} w="100%" justifyContent="space-between">
                      <VStack alignItems="flex-start" gap="4px">
                        <Text fontSize="12px">{accountServiceNameObj?.[account.address] || account.label}</Text>
                        {/* <Text fontSize="14px" color="text_caption">
                          {accountServiceNameObj?.[account.address] || addressAbridge(account.address)}
                        </Text> */}
                        <AddressCopyLink
                          address={account.address}
                          color="text_caption"
                          fontSize="14px"
                          onClickLink={() => {
                            window.open(getExplorerUrl(account.address, 'account'), '_blank')
                          }}
                          wrapStyle={{ gap: '8px' }}
                          iconGap="8px"
                        />
                      </VStack>

                      <Button
                        onClick={e => {
                          cancelBubble(e)
                          switchAccount({ account })
                          onClose()
                          onOuterClose()
                        }}
                        isDisabled={account.address === currentAccount.address}
                        color="text_highlight"
                        h="22px"
                        variant="outline"
                        borderRadius="4px"
                        fontSize="12px"
                      >
                        Switch
                      </Button>
                    </HStack>
                  )
                })}
              </VStack>
            </PopoverContent>
          )}
        </Popover>
      )}
    </HStack>
  )
}
